import React from 'react';
import { useNavigate } from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';
import GradientBackground from '../GradientBackground';
import { Box, Button, Typography } from '@mui/material';
import DynamicBarChart from '../DynamicBarChart';
import logo from '../logos/logo_completo.png';

const Home: React.FC = () => {
  const navigate = useNavigate();

  const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const userId = uuidv4();
      const formData = new FormData();
      formData.append("file", file);

      try {
        const response = await fetch(`http://localhost:8000/upload-csv/${userId}`, {
          method: "POST",
          body: formData,
        });
  
        if (response.ok) {
          const data = await response.json();
          navigate(`/chatbot/${userId}`);
        } else {
          const errorData = await response.json();
          console.error("Upload failed:", errorData.error);
        }
      } catch (error) {
        console.error("Error uploading file:", error);
      }
    }
  };

  return (
    <Box sx={{ textAlign: 'center', position: 'relative', height: '100vh', overflow: 'hidden' }}>
      <GradientBackground />

      <Box sx={{ paddingTop: 10, position: 'relative', zIndex: 1 }}>
        {/* Introductory Text */}
        <Typography variant="h4" color="white" gutterBottom>
          Analyze and modify your data effortlessly with our smart data assistant.
        </Typography>

        {/* Centered Logo (in place of "DATA TALK" title) */}
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: 5 }}>
          <Box
            sx={{
              backgroundColor: 'white',
              padding: 0.2,
              borderRadius: '18px',
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <img src={logo} alt="Logo" style={{ width: 325 }} />
          </Box>
        </Box>

        <Box sx={{ marginTop: 6 }}>
          <Button
            variant="contained"
            component="label"
            size="large"
          >
            Upload CSV File
            <input
              type="file"
              accept=".csv"
              hidden
              onChange={handleFileUpload}
            />
          </Button>
        </Box>
      </Box>

      {/* Dynamic Bar Chart (Sticks to Bottom) */}
      <DynamicBarChart />
    </Box>
  );
};

export default Home;
