import React, { useState, useEffect, useRef } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  TextField,
  Button,
  Paper,
  Avatar,
  CircularProgress
} from '@mui/material';
import GradientBackground from '../GradientBackground';
import logo from '../logos/logo.png';
import complete_logo from '../logos/logo_completo.png'
import '@fontsource/montserrat';
const Chatbot: React.FC = () => {
  const { userId } = useParams<{ userId: string }>(); // Read userId from URL
  const navigate = useNavigate();

  // Redirect to home if userId is missing
  useEffect(() => {
    if (!userId) {
      navigate('/');
    }
  }, [userId, navigate]);

  const [messages, setMessages] = useState<{ sender: string; text: string | JSX.Element }[]>([]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false); // Loading state to manage user input
  const [dataImageUrl, setDataImageUrl] = useState<string | null>(null); // State to hold the image URL for the data head
  const [csvDownloaded, setCsvDownloaded] = useState(false); // State to manage download button enabled/disabled
  const messagesEndRef = useRef<HTMLDivElement | null>(null);

  const parseTextFormat = (text: string): JSX.Element => {
    return (
      <span style={{ display: 'block', marginBottom: '0.5rem' }}>
        {text.split('\n\n').map((paragraph, index) => (
          <span key={index} style={{ display: 'block', marginBottom: '0.5rem' }}>
            {paragraph.split('\n').map((line, i) => (
              <React.Fragment key={i}>
                {line.split(/(\*\*.*?\*\*)/).map((segment, j) => {
                  if (segment.startsWith("**") && segment.endsWith("**")) {
                    // Remove the ** and wrap in <strong> for bold effect
                    return <strong key={j}>{segment.slice(2, -2)}</strong>;
                  }
                  return segment;
                })}
                <br />
              </React.Fragment>
            ))}
          </span>
        ))}
      </span>
    );
  };
  

  const fetchDataHeadImage = async () => {
    try {
      const imgResponse = await fetch(`http://localhost:8000/data-head-image/${userId}`);
      if (!imgResponse.ok) {
        throw new Error('Failed to fetch data head image');
      }
      const blob = await imgResponse.blob();
      const imageUrl = URL.createObjectURL(blob);
      setDataImageUrl(imageUrl);
    } catch (error) {
      console.error('Error fetching data head image:', error);
    }
  };

  useEffect(() => {
    // Fetch the data head image when the component loads
    if (userId) {
      fetchDataHeadImage();
    }
  }, [userId]);

  const handleSendMessage = async () => {
    if (inputValue.trim() === '') return;

    // Display the user message in the chat
    setMessages((prevMessages) => [...prevMessages, { sender: 'user', text: inputValue }]);
    const userMessage = inputValue;
    setInputValue(''); // Clear the input field
    setIsLoading(true); // Start loading

    try {
      // Send the message to the backend LLM API
      const response = await fetch(`http://localhost:8000/chat/${userId}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ prompt: userMessage }),
      });

      if (!response.ok) {
        throw new Error('Error communicating with the backend');
      }

      // Check if the response is an image
      if (response.headers.get('content-type')?.includes('image/png')) {
        const blob = await response.blob();
        const imageUrl = URL.createObjectURL(blob);

        // If the response is an image, add it to the messages as an image element
        setMessages((prevMessages) => [
          ...prevMessages,
          {
            sender: 'bot',
            text: (
              <Box component="img" src={imageUrl} alt="Chart" sx={{ width: '100%', maxWidth: 400, height: 'auto', borderRadius: 2 }} />
            )
          },
        ]);
      } else {
        // Otherwise, handle as a JSON text response
        const data = await response.json();
        setMessages((prevMessages) => [
          ...prevMessages,
          { sender: 'bot', text: parseTextFormat(data.response) },
        ]);
      }

      // Fetch the data head image after receiving the bot's response
      await fetchDataHeadImage();
      setCsvDownloaded(true); // Enable the download button after response

    } catch (error) {
      console.error('Error:', error);
      setMessages((prevMessages) => [
        ...prevMessages,
        { sender: 'bot', text: 'Error: Unable to get response from the server.' },
      ]);
    } finally {
      setIsLoading(false); // Stop loading after response is received
    }
  };
  const handleDownloadCharts = async () => {
    try {
      const response = await fetch(`http://localhost:8000/download-charts/${userId}`, {
        method: 'GET',
      });
      if (!response.ok) {
        throw new Error('Failed to download charts');
      }

      const blob = await response.blob();
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `${userId}_charts.zip`; // Name the download file based on user ID or other identifier
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);

    } catch (error) {
      console.error('Error downloading Charts:', error);
    }
  };
  const handleDownloadCsv = async () => {
    try {
      const response = await fetch(`http://localhost:8000/download-csv/${userId}`, {
        method: 'GET',
      });
      if (!response.ok) {
        throw new Error('Failed to download CSV');
      }

      const blob = await response.blob();
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = 'modified_data.csv';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);

    } catch (error) {
      console.error('Error downloading CSV:', error);
    }
  };

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  return (
    <Box sx={{ position: 'relative', minHeight: '100vh', overflow: 'hidden' }}>
      {/* Background */}
      <Box sx={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', zIndex: -1 }}>
        <GradientBackground />
      </Box>

      {/* Content */}
      <Box sx={{ paddingTop: 5, position: 'relative', zIndex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center', gap: 4, minHeight: '100vh' }}>
        {/* Display Session ID */}
        <Typography variant="h5" color="white" gutterBottom>
          {userId ? `Session ID: ${userId}` : 'No user session'}
        </Typography>

        {/* Display the data head image */}
        {dataImageUrl && (
          <Box
            component="img"
            src={dataImageUrl}
            alt="Data Head"
            sx={{
              width: '100%',
              maxWidth: 300,
              height: 'auto',
              borderRadius: 2,
              boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)',
              marginBottom: 4,
            }}
          />
        )}

        {/* Title and Buttons */}
        <Box sx={{ textAlign: 'center', marginBottom: 4 }}>
        <Box
            sx={{
              backgroundColor: 'white',
              padding: 0.2,
              borderRadius: '18px',
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <img src={complete_logo} alt="Logo" style={{ width: 275 }} />
          </Box>
          <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2, marginTop: 4 }}>
            <Button variant="contained" color="primary" onClick={handleDownloadCharts}>
              Download Charts
            </Button>
            <Button
              variant="contained"
              color="secondary"
              onClick={handleDownloadCsv}
              disabled={!csvDownloaded}
            >
              Download CSV
            </Button>
          </Box>
        </Box>

        {/* Chat Messages */}
        <Box sx={{ width: '75%', maxHeight: '80vh', overflowY: 'auto', backgroundColor: 'rgba(255, 255, 255, 0.1)', borderRadius: 2, padding: 2 }}>
          {messages.map((message, index) => (
            <Box key={index} sx={{ display: 'flex', justifyContent: message.sender === 'user' ? 'flex-end' : 'flex-start', marginBottom: 2 }}>
              <Paper sx={{ padding: 2, maxWidth: '60%', backgroundColor: message.sender === 'user' ? '#e0f7fa' : '#f1f1f1', borderRadius: '20px' }}>
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  {message.sender === 'bot' && (
                    <Box
                      component="img"
                      src={logo}
                      alt="Logo"
                      sx={{
                        marginRight: 2,
                        width: 60,
                        height: 60,
                        borderRadius: '50%',
                        objectFit: 'contain',
                      }}
                    />
                  )}
                  <Typography component="div">
                    {typeof message.text === 'string' ? message.text : message.text}
                  </Typography>
                  {message.sender === 'user' && (
                    <Avatar sx={{ marginLeft: 2, backgroundColor: '#2196f3', width: 40, height: 40 }}>
                      U
                    </Avatar>
                  )}
                </Box>
              </Paper>
            </Box>
          ))}
        </Box>

        {/* Chat Input */}
        <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2, width: '80%', backgroundColor: 'rgba(255, 255, 255, 0.2)', padding: 2, borderRadius: 2, marginBottom: 4 }}>
          <TextField
            fullWidth
            variant="outlined"
            placeholder="Enter your message..."
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            onKeyPress={(e) => {
              if (e.key === 'Enter' && !isLoading) {
                handleSendMessage();
              }
            }}
            sx={{ backgroundColor: 'white', borderRadius: 1 }}
            disabled={isLoading} // Disable input while loading
          />
          <Button variant="contained" onClick={handleSendMessage} disabled={isLoading}>
            {isLoading ? <CircularProgress size={24} /> : 'Send'}
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default Chatbot;
